<!-- Card.vue -->
<template>
  <router-link class="card-link" :to="{ name: 'box', params: { slug: box.slug } }">
    <div class="card">
      <!-- –û—Å–Ω–æ–≤–Ω–∞—è –æ–±—ë—Ä—Ç–∫–∞ —Å –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ–º -->
      <div class="card-wrapper">
        <img :src="coverSrc" class="cover-image" :alt="box.name" />
        
        <!-- –ì—Ä–∞–¥–∏–µ–Ω—Ç –¥–ª—è —á–∏—Ç–∞–µ–º–æ—Å—Ç–∏ —Ç–µ–∫—Å—Ç–∞ -->
        <div class="card-gradient"></div>
        
        <!-- –ë—ã—Å—Ç—Ä—ã–µ –¥–µ–π—Å—Ç–≤–∏—è (–ø–æ–∫–∞–∑—ã–≤–∞—é—Ç—Å—è –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏) -->
        <div class="quick-actions">
          <button 
            class="action-btn favorite-btn"
            @click.prevent="toggleFavorite"
            :title="isFavorite ? '–£–±—Ä–∞—Ç—å –∏–∑ –∏–∑–±—Ä–∞–Ω–Ω–æ–≥–æ' : '–í –∏–∑–±—Ä–∞–Ω–Ω–æ–µ'"
          >
            <svg 
              width="20" 
              height="20" 
              viewBox="0 0 24 24" 
              :fill="isFavorite ? '#35C66B' : 'none'" 
              :stroke="isFavorite ? '#35C66B' : '#fff'" 
              stroke-width="2"
              stroke-linecap="round" 
              stroke-linejoin="round"
            >
              <path d="M20.84 4.61a5.5 5.5 0 0 0-7.78 0L12 5.67l-1.06-1.06a5.5 5.5 0 0 0-7.78 7.78l1.06 1.06L12 21.23l7.78-7.78 1.06-1.06a5.5 5.5 0 0 0 0-7.78z"/>
            </svg>
          </button>
          <button 
            class="action-btn cart-btn"
            @click.prevent="addToCart"
            title="–í –∫–æ—Ä–∑–∏–Ω—É"
            :disabled="isAddingToCart"
          >
            <svg 
              v-if="!isAddingToCart"
              width="20" 
              height="20" 
              viewBox="0 0 24 24" 
              fill="none" 
              stroke="#fff" 
              stroke-width="2"
              stroke-linecap="round" 
              stroke-linejoin="round"
            >
              <circle cx="9" cy="21" r="1"/>
              <circle cx="20" cy="21" r="1"/>
              <path d="M1 1h4l2.68 13.39a2 2 0 0 0 2 1.61h9.72a2 2 0 0 0 2-1.61L23 6H6"/>
            </svg>
            <span v-else class="cart-checkmark">‚úì</span>
          </button>
        </div>
      </div>

      <!-- –ù–∞–∑–≤–∞–Ω–∏–µ –∫–∞–∫ –∫–∞—Ä—Ç–∏–Ω–∫–∞ (–¥–ª—è –æ—Å–æ–±–æ–≥–æ —Å—Ç–∏–ª—è) -->
      <!-- <img v-if="box.titleImage" :src="box.titleImage" class="title" :alt="box.name" />
      <div v-else class="title-text white-text montserrat-bold">{{ box.name }}</div> -->
      <!-- –ü–µ—Ä—Å–æ–Ω–∞–∂/–¥–µ–∫–æ—Ä (–ø–æ—è–≤–ª—è–µ—Ç—Å—è –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏) -->
      <!-- <img v-if="box.characterImage" :src="box.characterImage" class="character" alt="" /> -->

      <!-- –ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏–æ–Ω–Ω—ã–π –±–ª–æ–∫ (–≤—Å–µ–≥–¥–∞ –≤–∏–¥–µ–Ω) -->
      <div class="card-info">
        <div class="card-title white-text montserrat-bold">{{ box.name }}</div>
        <div class="info-top">
          <span class="emotion-tag montserrat-medium">{{ box.emotion }}</span>
          <span class="price montserrat-bold">{{ formatPrice(boxPrice) }} ‚ÇΩ</span>
        </div>
        <div class="composition">
          <template v-for="(item, index) in shortComposition" :key="index">
            <span class="composition-item montserrat-regular">{{ item }}</span>
            <span v-if="index < shortComposition.length - 1" class="dot">‚Ä¢</span>
          </template>
        </div>
      </div>
    </div>
  </router-link>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue'
// –ï—Å–ª–∏ –≤ –ø—Ä–æ–µ–∫—Ç–µ –µ—Å—Ç—å —ç—Ç–∏ —É—Ç–∏–ª–∏—Ç—ã ‚Äî —Ä–∞—Å–∫–æ–º–º–µ–Ω—Ç–∏—Ä—É–π—Ç–µ
import { calculateBoxPrice, getShortComposition } from '@/data/boxes'
import { cartUtils } from '@/utils/cartUtils'
import { favoritesUtils } from '@/utils/favoritesUtils'

const props = defineProps({
  box: {
    type: Object,
    required: true
  }
})

const isFavorite = ref(false)
const isAddingToCart = ref(false)

// –í–∏–∑—É–∞–ª—å–Ω—ã–µ –∏—Å—Ç–æ—á–Ω–∏–∫–∏ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π ‚Äî –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ–º –æ–±–∞ –≤–∞—Ä–∏–∞–Ω—Ç–∞ –∫–ª—é—á–µ–π (image / coverImage)
const coverSrc = computed(() => {
  return props.box.coverImage || props.box.image || ''
})

// –°—Å—ã–ª–∫–∞: –µ—Å–ª–∏ –µ—Å—Ç—å slug ‚Äî –∏—Å–ø–æ–ª—å–∑—É–µ–º, –∏–Ω–∞—á–µ '#'
const linkHref = computed(() => {
  return props.box.slug ? `/box/${props.box.slug}` : '#'
})

// –¶–µ–Ω–∞ –±–æ–∫—Å–∞ ‚Äî –∏—Å–ø–æ–ª—å–∑—É–µ–º –≤–Ω–µ—à–Ω–∏–π calculateBoxPrice –µ—Å–ª–∏ –µ—Å—Ç—å, –∏–Ω–∞—á–µ fallback –Ω–∞ box.price
const boxPrice = computed(() => {
  try {
    // –µ—Å–ª–∏ —Ñ—É–Ω–∫—Ü–∏—è –ø—Ä–∏—Å—É—Ç—Å—Ç–≤—É–µ—Ç –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –≤–∞–ª–∏–¥–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ ‚Äî –∏—Å–ø–æ–ª—å–∑—É–µ–º –µ—ë
    const p = calculateBoxPrice ? calculateBoxPrice(props.box.id) : undefined
    return (typeof p === 'number' && !isNaN(p)) ? p : (props.box.price ?? 0)
  } catch (e) {
    return props.box.price ?? 0
  }
})

// –ö–æ—Ä–æ—Ç–∫–∏–π —Å–æ—Å—Ç–∞–≤ –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è ‚Äî –∏—Å–ø–æ–ª—å–∑—É–µ–º –≤–Ω–µ—à–Ω–∏–π getShortComposition –µ—Å–ª–∏ –µ—Å—Ç—å, –∏–Ω–∞—á–µ –±–µ—Ä–µ–º box.shortComposition
const shortComposition = computed(() => {
  try {
    const arr = getShortComposition ? getShortComposition(props.box.id) : undefined
    if (Array.isArray(arr) && arr.length) return arr
  } catch (e) {
    // ignore
  }
  if (Array.isArray(props.box.shortComposition) && props.box.shortComposition.length) {
    return props.box.shortComposition
  }
  // –í –∫—Ä–∞–π–Ω–µ–º —Å–ª—É—á–∞–µ –ø–æ–ø—Ä–æ–±—É–µ–º —Å–æ–±—Ä–∞—Ç—å –∏–∑ box.composition (–µ—Å–ª–∏ —ç—Ç–æ —Å—Ç—Ä–æ–∫–∞/–º–∞—Å—Å–∏–≤)
  if (Array.isArray(props.box.composition)) return props.box.composition.slice(0, 3)
  if (typeof props.box.composition === 'string') {
    return props.box.composition.split(',').map(s => s.trim()).filter(Boolean).slice(0, 3)
  }
  return []
})

// –ü–µ—Ä–µ–∫–ª—é—á–∏—Ç—å –∏–∑–±—Ä–∞–Ω–Ω–æ–µ
const toggleFavorite = () => {
  // –ï—Å–ª–∏ –µ—Å—Ç—å —É—Ç–∏–ª–∏—Ç–∞ favoritesUtils ‚Äî –∏—Å–ø–æ–ª—å–∑—É–µ–º –µ—ë:
  const added = favoritesUtils.toggle(props.box.id)
  isFavorite.value = added

  // –ò–Ω–∞—á–µ ‚Äî –ø—Ä–æ—Å—Ç–∞—è –ª–æ–∫–∞–ª—å–Ω–∞—è –ª–æ–≥–∏–∫–∞ (–ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏–µ)
  // const added = !isFavorite.value
  // isFavorite.value = added

  console.log(added ? '–î–æ–±–∞–≤–ª–µ–Ω–æ –≤ –∏–∑–±—Ä–∞–Ω–Ω–æ–µ' : '–£–¥–∞–ª–µ–Ω–æ –∏–∑ –∏–∑–±—Ä–∞–Ω–Ω–æ–≥–æ', props.box.name)
}

// –î–æ–±–∞–≤–∏—Ç—å –≤ –∫–æ—Ä–∑–∏–Ω—É
const addToCart = () => {
  if (isAddingToCart.value) return

  isAddingToCart.value = true

  // –ï—Å–ª–∏ –µ—Å—Ç—å cartUtils ‚Äî –º–æ–∂–Ω–æ –≤—ã–∑–≤–∞—Ç—å cartUtils.addBox(props.box.id)
  cartUtils.addBox(props.box.id)
  console.log('–î–æ–±–∞–≤–ª–µ–Ω–æ –≤ –∫–æ—Ä–∑–∏–Ω—É:', props.box.name)

  // –í–∏–∑—É–∞–ª—å–Ω–∞—è –∏–Ω–¥–∏–∫–∞—Ü–∏—è —É—Å–ø–µ—Ö–∞
  setTimeout(() => {
    isAddingToCart.value = false
  }, 1500)
}

// –ü–µ—Ä–µ–π—Ç–∏ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É –±–æ–∫—Å–∞
const navigateToBox = () => {
  // –°–Ω–∞—á–∞–ª–∞ –ø—Ä–æ–±—É–µ–º –ø–µ—Ä–µ–π—Ç–∏ —á–µ—Ä–µ–∑ slug, –µ—Å–ª–∏ –µ—Å—Ç—å
  if (props.box.slug) {
    // –ü–æ–ø—ã—Ç–∫–∞ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å history API (—Ä–æ—É—Ç–µ—Ä) ‚Äî –µ—Å–ª–∏ —É –≤–∞—Å –µ—Å—Ç—å Vue Router, –∑–∞–º–µ–Ω–∏—Ç–µ –Ω–∞ router.push
    try {
      window.location.href = `/box/${props.box.slug}`
    } catch (e) {
      console.log('Navigate to:', `/box/${props.box.slug}`)
    }
  } else {
    // –ù–µ—Ç —Å—Ç—Ä–∞–Ω–∏—Ü—ã ‚Äî –º–æ–∂–Ω–æ –æ—Ç–∫—Ä—ã—Ç—å —Å—Å—ã–ª–∫—É –ø–æ href (–≤ –Ω–∞—à–µ–º —Å–ª—É—á–∞–µ '#')
    console.log('No slug for box:', props.box.name)
  }
}

// –§–æ—Ä–º–∞—Ç–∏—Ä–æ–≤–∞—Ç—å —Ü–µ–Ω—É
const formatPrice = (price) => {
  const p = Number(price) || 0
  return p.toLocaleString('ru-RU')
}

// –ü—Ä–æ–≤–µ—Ä–∏—Ç—å –∏–∑–±—Ä–∞–Ω–Ω–æ–µ –ø—Ä–∏ –º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ (–µ—Å–ª–∏ –µ—Å—Ç—å —É—Ç–∏–ª–∏—Ç—ã)
onMounted(() => {
  if (favoritesUtils && favoritesUtils.isFavorite) {
    isFavorite.value = favoritesUtils.isFavorite(props.box.id)
  } else if (props.box.isFavorite) {
    isFavorite.value = !!props.box.isFavorite
  }
  if (props.box.isFavorite) {
    isFavorite.value = !!props.box.isFavorite
  }
})
</script>


<style scoped>
.card-link {
  text-decoration: none;
  color: inherit;
  display: block;
}

.card-title{
  font-size: 1.2rem;
}

.card {
  width: 270px;
  height: 420px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  perspective: 2500px;
  transition: transform 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
}

.cover-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border: 3px solid #fff;
  border-radius: 16px;
}

.card-wrapper {
  transition: all 0.5s cubic-bezier(0.23, 1, 0.320, 1);
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  top: 0;
  left: 0;
}

.card-gradient {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 60%;
  background: linear-gradient(
    to bottom,
    transparent 0%,
    rgba(0, 0, 0, 0.3) 40%,
    rgba(0, 0, 0, 0.8) 100%
  );
  border-radius: 0 0 13px 13px;
  opacity: 1;
  transition: opacity 0.5s;
}

.card:hover .card-gradient {
  opacity: 0.7;
}

/* –ë—ã—Å—Ç—Ä—ã–µ –¥–µ–π—Å—Ç–≤–∏—è */
.quick-actions {
  position: absolute;
  top: 15px;
  right: 15px;
  display: flex;
  gap: 10px;
  opacity: 0;
  transform: translateY(-10px);
  transition: all 0.4s ease;
  z-index: 10;
}

.card:hover .quick-actions {
  opacity: 1;
  transform: translateY(0);
}

.action-btn {
  width: 42px;
  height: 42px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
}

.action-btn:hover {
  background: rgba(255, 255, 255, 0.25);
  transform: scale(1.1);
}

.favorite-btn:hover {
  background: rgba(53, 198, 107, 0.2);
  border-color: #35C66B;
}

.cart-btn:hover {
  background: rgba(53, 198, 107, 0.2);
  border-color: #35C66B;
}

/* –ù–∞–∑–≤–∞–Ω–∏–µ */
.title {
  width: 90%;
  transition: transform 0.5s cubic-bezier(0.23, 1, 0.320, 1);
  padding: 0 20px;
  z-index: 3;
  position: relative;
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.5));
}

.card:hover .title {
  transform: translate3d(0%, -60px, 100px);
}

/* –ü–µ—Ä—Å–æ–Ω–∞–∂ */
.character {
  width: 90%;
  opacity: 0;
  transition: all 0.5s cubic-bezier(0.23, 1, 0.320, 1);
  position: absolute;
  z-index: 2;
  filter: drop-shadow(0 4px 12px rgba(0, 0, 0, 0.6));
}

.card:hover .character {
  opacity: 1;
  transform: translate3d(0%, -35%, 100px);
}

/* –ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏–æ–Ω–Ω—ã–π –±–ª–æ–∫ */
.card-info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 20px 18px;
  z-index: 4;
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.95) 0%,
    rgba(0, 0, 0, 0.7) 60%,
    transparent 100%
  );
  border-radius: 0 0 13px 13px;
  transition: all 0.5s ease;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.card:hover .card-info {
  padding-bottom: 25px;
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.98) 0%,
    rgba(0, 0, 0, 0.8) 70%,
    transparent 100%
  );
}

.info-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.emotion-tag {
  font-size: 0.75rem;
  color: #35C66B;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  background: rgba(53, 198, 107, 0.15);
  padding: 4px 10px;
  border-radius: 20px;
  border: 1px solid rgba(53, 198, 107, 0.3);
}

.price {
  font-size: 1.25rem;
  font-weight: 700;
  color: #fff;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

.composition {
  display: flex;
  align-items: center;
  gap: 8px;
  flex-wrap: wrap;
  opacity: 0.9;
}

.composition-item {
  font-size: 0.85rem;
  color: #D9D9D9;
  font-weight: 500;
}

.dot {
  color: #35C66B;
  font-weight: 700;
}
</style>