import {products} from '@/data/products'
import {calculateBoxPrice } from '@/data/boxes'

const CART_KEY = 'box_cart'

export const cartUtils = {
  // –ü–æ–ª—É—á–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É –∏–∑ localStorage
  getCart() {
    try {
      const cart = localStorage.getItem(CART_KEY)
      return cart ? JSON.parse(cart) : { items: [] }
    } catch (e) {
      console.error('Error reading cart:', e)
      return { items: [] }
    }
  },

  // –°–æ—Ö—Ä–∞–Ω–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É –≤ localStorage
  saveCart(cart) {
    try {
      localStorage.setItem(CART_KEY, JSON.stringify(cart))
    } catch (e) {
      console.error('Error saving cart:', e)
    }
  },

  // –î–æ–±–∞–≤–∏—Ç—å –±–æ–∫—Å –≤ –∫–æ—Ä–∑–∏–Ω—É (–¥–µ—Ñ–æ–ª—Ç–Ω—ã–π —Å–æ—Å—Ç–∞–≤)
  addBox(boxId) {
    const cart = this.getCart()
    const cartItem = {
      id: `cart-${Date.now()}-${Math.random()}`,
      boxId,
      customComposition: null, // null = –¥–µ—Ñ–æ–ª—Ç–Ω—ã–π —Å–æ—Å—Ç–∞–≤
      totalPrice: calculateBoxPrice(boxId),
      addedAt: Date.now()
    }
    cart.items.push(cartItem)
    this.saveCart(cart)
    return cartItem
  },

  // –û–±–Ω–æ–≤–∏—Ç—å —Å–æ—Å—Ç–∞–≤ –±–æ–∫—Å–∞ –≤ –∫–æ—Ä–∑–∏–Ω–µ (–ø–æ—Å–ª–µ –∫–∞—Å—Ç–æ–º–∏–∑–∞—Ü–∏–∏ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ –±–æ–∫—Å–∞)
  updateBoxComposition(cartItemId, customComposition) {
    const cart = this.getCart()
    const item = cart.items.find(i => i.id === cartItemId)
    if (item) {
      item.customComposition = customComposition
      // –ü–µ—Ä–µ—Å—á–∏—Ç—ã–≤–∞–µ–º —Ü–µ–Ω—É
      item.totalPrice = customComposition.reduce((sum, comp) => {
        const product = products[comp.productId]
        return sum + (product.price * comp.quantity)
      }, 0)
      this.saveCart(cart)
    }
  },

  // –£–¥–∞–ª–∏—Ç—å —ç–ª–µ–º–µ–Ω—Ç –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã
  removeItem(cartItemId) {
    const cart = this.getCart()
    cart.items = cart.items.filter(i => i.id !== cartItemId)
    this.saveCart(cart)
  },

  // –û—á–∏—Å—Ç–∏—Ç—å –≤—Å—é –∫–æ—Ä–∑–∏–Ω—É
  clear() {
    this.saveCart({ items: [] })
  },

  // –ü–æ–ª—É—á–∏—Ç—å –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –≤ –∫–æ—Ä–∑–∏–Ω–µ
  getItemsCount() {
    const cart = this.getCart()
    return cart.items.length
  },

  // –ù–∞–π—Ç–∏ —ç–ª–µ–º–µ–Ω—Ç –≤ –∫–æ—Ä–∑–∏–Ω–µ –ø–æ boxId
  findByBoxId(boxId) {
    const cart = this.getCart()
    return cart.items.find(item => item.boxId === boxId)
  }
}

